@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
.authForm_container {
    width: 100%;
    max-width: 420px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 30px;
    padding: 20px;
}
.auth_input {
    max-width: 320px;
    width: 100%;
    padding: 10px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 1);
    margin-bottom: 14px;
    font-size: 12px;
    color: black;
}

.auth_submit {
    max-width: 320px;
    width: 100%;
    padding: 10px;
    border-radius: 30px;
    text-align: center;
    background: #04aaff;
    color: white;
    margin-top: 10;
    cursor: pointer;
}
.auth_noneError {
    color: black;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
}
.auth_error {
    color: tomato;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
}

.auth_switch {
    color: #04aaff;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 50px;
    display: block;
    font-size: 12px;
    text-decoration: underline;
}
.auth_container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
  }
  
  .auth_buttons {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 320px;
  }
  
  .auth_button {
    cursor: pointer;
    border-radius: 20px;
    border: none;
    padding: 10px 0px;
    font-size: 12px;
    text-align: center;
    width: 150px;
    background: white;
    cursor: pointer;
  }
.tweet_container {
    background-color: white;
    color: black;
    margin: 50px 10px 50px 10px;
    padding: 20px;
    border-radius: 20px;
}
.tweet_user {
    margin-bottom: 10px;
    font-size: large;
    font-weight: 500;
}
.tweet_text {
    margin: 20px;
}
.tweet_time {
    margin-bottom: 10px;
    color: rgb(158, 157, 157);
    float: right;
}
.tweet_action span {
    margin: 0 10px 0 10px;
}
.tweet_action_none {
    visibility: hidden;
    pointer-events: none;
}
.tweet_container img {
    margin: auto;
    margin-bottom: 10px;
    max-width: 300px;
    width: 100%;    
    cursor: pointer;
    display: flex;
}
.tweet_container img:active {
    transform: scale(2.5);
}
.main_container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 45px;
    flex-direction: column;
}
.factory_form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}
.factory_input_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 20px;
    width: 100%;
}
.factory_input {
    flex-grow: 1;
    height: 50px;
    padding: 0px 20px;
    color: white;
    border: 1px solid #04aaff;
    border-radius: 25px;
    font-weight: 500;
    font-size: 12px;
}
.factory_submit {
    position: absolute;
    right: 0;
    background-color: #04aaff;
    height: 50px;
    width: 50px;
    padding: 10px 0px;
    text-align: center;
    border-radius: 25px;
    color: white;
}
.factoryInput_label {
    color: #04aaff;
    cursor: pointer;
    z-index: -1;
}
.factoryInput_label span {
    margin-right: 10px;
    font-size: 12px;
}
.factoryForm_file {
    position: relative;
    top: -20px;
    cursor: pointer;
} 
.factoryForm_attachment {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.factoryForm_attachment img {
    max-width: 200px;
    width: 100%;
    border-radius: 10px;
}
  
.factoryForm_clear {
    color: #04aaff;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5px;
}

.factoryForm_clear span {
    margin-right: 10px;
    font-size: 12px;
}
.home_container {
    margin: auto;
    max-width: 550px;
    width: 100%;
}

.profile_container {
    border-bottom: 1px solid rgba(255, 255, 255, 0.9);
    padding-bottom: 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.profile_form {
    margin: 20px;
    display: flex;
    justify-content: center;

}  
.profile_input {
    background-color: white;
    color: black;
    border: none;
    border-radius: 20px;
    margin-right: 15px;
    padding: 6px;
    font-size: 14px;
    line-height: 16 px;
}
.profile_submit {
    color: #04aaff;
    cursor: pointer;
}
.profile_submit:hover {
    color: tomato
}
.profile_logout {
    display: flex;
    justify-content: center;
}
.logout {
    border: none;
    border-radius: 20px;
    background-color: #04aaff;
    color: white;
    padding: 5px;
    text-align: center;
    cursor: pointer;
}
.logout:hover {
    background-color: tomato;
    color: black;
}
* {
    box-sizing: border-box;
}
input {
    all: unset;
    box-sizing: border-box;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
  }
  
  body {
    width: 100%;
    background-color: black;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 14px;
    color: white;
  }
  
  button {
    background-color: white;
    color: black;
  }
  
  a {
    text-decoration: none;
    color: inherit;
  }
  
  form {
    width: 100%;
  }
  .container {
    margin: auto;
    width: 100%;
    max-width: 420px;
    display: flex;
    flex-direction: column;
  }
